<template>
  <Header appName="Grocery List"></Header>
  <AddItem @addItem="addItem"></AddItem>
  <Items :items=myList @deleteItem=deleteItem @toggleDone=toggleDone></Items>
  <Footer/>
</template>

<script>
import Header from '@/components/Header.vue'
import AddItem from '@/components/AddItem.vue'
import Items from '@/components/Items.vue'
import Footer from '@/components/Footer.vue'

const server = 'http://fhkiel.eu-4.evennode.com/items'

export default {
  name: 'Home',
  components: {
    Header,
    AddItem,
    Items,
    Footer
  },
  data() {
    return {
      newItem: "",
      myList: []
    }
  },
  methods: {
    
    async loadItems() {
      const url = server+"/items"
      console.log("URL: " + url)

      const result =  await fetch(url)
      this.myList = await result.json()
      
      console.log(this.myList)
    },
    async addItem(itemContent) {
      console.log("adding item: " + itemContent)

      // var dateStamp = new Date();
      var newItem = {}

      // newItem.id = dateStamp.getTime() 
      newItem.content = itemContent
      newItem.done = false

      const url = server + "/items"
      const result = await fetch(url, {
          method :  "post",
          headers:  {'Content-Type': 'application/json'},
          body:     JSON.stringify(newItem)
      })

      await this.loadItems()

    },
    async deleteItem(itemId ) {
      const url = server + "/items/" + itemId
      console.log("deleting item " + url)
      
      const result = await fetch(url, {
          method :  "delete"
      })
      await this.loadItems()

    },
    async toggleDone(itemId) {
      const url = server + "/items/" + itemId
      
      var i = this.myList.findIndex(item => item._id== itemId)
      console.log("i = " + i)

      var updItem = {}
      updItem.done = !this.myList[i].done

      console.log("toggling " + url)
      
      const result = await fetch(url, {
          method :  "put",
          headers:  {'Content-Type': 'application/json'},
          body:     JSON.stringify(updItem)
      })
      console.log(result)

      await this.loadItems()
    }
  },
  async mounted() {
    await this.loadItems()

  }
}
</script>

